
import React from 'react'

const About = () => {
    return (
        <div >
            <div className="About-details">
                <div className="About-paragraph">

                    This is a simple-styled React project app that fetches gifs based on a keyword of your choice. The app created as a requirement for a Web Developer Course provided by Academy Xi and it consists of the Component below :

                </div>

                <di>
                    <ul className="About-li">
                        <li>
                            Footer Gifs - About
                        </li>
                        <li>
                            GifComponent - LikerCounter
                        </li>
                        <li>
                            Mock UP API - SearchGif
                        </li>
                        <li>
                            List of Gif - MinionList
                        </li>
                    </ul>
                </di>
                <div>
                    There is also one container file that returns two of the above components and its named ContainerGifList. 
                </div>
                {/* <div className="About-paragraph2">
                    Give it a try by searching your favourite Gif using a keyword of your choice! 
                </div> */}
            </div>
        </div>
    )
}
export default About


