
import LikerCounter from './LikerCounter'
import { v4 } from 'uuid'
import React, { useEffect, useState } from 'react'

const GifComponent = ({ gif }) => {
    const [counter, setCounter] = useState(0)

    // This useEffect  
    useEffect(() =>{
        const data = localStorage.getItem('liker'); 
        if (data) {
          setCounter(JSON.parse(data)); // if we have data => then I'm going to parse it and set it as a counter - setCounter is a stringified version of the counter, therefore, we need to parse it. 
        }
     }, []);

    // This useEffect , will take the initial state (counter) and save it in the local storage as a string using JSON.stringify
    useEffect(() =>{
       localStorage.setItem('liker', JSON.stringify(counter)) 
    });
   
    return (
        <div className='gifsList'>
            <div key={v4()}>
                <img className="gifImage" src={gif} alt="gifs-images" />
                <LikerCounter counter={counter} setCounter={setCounter} gif={gif} />
            </div>
        </div>
    )
}
export default GifComponent 


